import { ToastrService } from 'ngx-toastr';
import { AddJudgeComponent } from './add-judge/add-judge.component';
import { JudgesService } from './../../services/judges.service';
import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { TooltipPosition } from '@angular/material/tooltip';
import { FormControl } from '@angular/forms';
import { ConfirmationService } from 'primeng/api';

@Component({
  selector: 'app-judge-list',
  templateUrl: './judge-list.component.html',
  styleUrls: ['./judge-list.component.scss'],
  providers: [ConfirmationService]

})
export class JudgeListComponent implements OnInit {

  judges = [  ]
  positionOptions: TooltipPosition[] = ['below', 'above', 'left', 'right'];
  tooltipPosition = new FormControl(this.positionOptions[0]);
  
  constructor(
    private judgeService:JudgesService,
    public dialog: MatDialog,
    private confirmationService: ConfirmationService, 
    private toastr: ToastrService

    ) { }

  ngOnInit(): void {
    this.getJudges();
  }

  getJudges(){
    this.judgeService.getAllJudges().subscribe((data)=>{
      console.log(data);
      this.judges = data.data;
      
    },(error)=>{
      console.log(error);
      
    })
  }

  openAddJudge(){
    const dialogRef = this.dialog.open(AddJudgeComponent, {
      width: '600px',
      // data: {name: this.name, animal: this.animal}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      this.getJudges();
      // this.animal = result;
    });
  }

  deleteJudgeConfirmation(judge){
    console.log('delete this', judge);
    
    this.confirmationService.confirm({
      message: 'Are you sure that you want to proceed?',
      header: 'Confirmation',
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
          console.log('accepted');
          this.judgeService.deleteJudge(judge.id).subscribe((data)=>{
            console.log(data);
            this.toastr.success(data.message);
            if(data.status === 200){
              this.getJudges();
            }
            
          },(error)=>{
            console.log(error);
            this.toastr.error(error.message);

          })     
          
      },
      reject: () => {
          console.log('rejected');

      }
  });
  }
 
  }

