import { JudgesService } from './../../../services/judges.service';
import { JudgeListComponent } from './../judge-list.component';
import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { ToastrService } from 'ngx-toastr';
import { ProductService } from 'src/app/services/product.service';
import * as S3 from 'aws-sdk/clients/s3';

@Component({
  selector: 'app-add-judge',
  templateUrl: './add-judge.component.html',
  styleUrls: ['./add-judge.component.scss']
})
export class AddJudgeComponent implements OnInit {

  judgeModel = {
    "name":"",
    "profile_pic":"",
    "email":""
  }
  files: any = [];
  fileNames: any = [];
    constructor(
      private toastr: ToastrService,
      private judgeService: JudgesService,
      public dialogRef: MatDialogRef<JudgeListComponent>,
      ) { }

  ngOnInit(): void {
  }

  uploadFile(event) {
    // this.uiLoader.start();
    
    console.log('event is', event);
    if (this.files.length === 0) {
      for (let index = 0; index < event.length; index++) {
        const element = event[index];
        this.files.push(element);
      }
      for (let index = 0; index < event.length; index++) {
        const element = event[index];
        this.fileNames.push(element.name);
      }

      console.log('final files ', this.files);
      this.upload();
    } else {
      this.toastr.warning('One file can be choosen at a time!');
      console.log('one file only');
    }
  }


   upload() {
    const file = this.files[0];
    // const file = this.files.item(0);
    //  let status =await this.uploadService.uploadFile(file);
    this.uploadImage(file);
    console.log('status is', status);
  }
  
  uploadImage(file) {
    if(typeof(file)== 'undefined'){
      this.toastr.warning('Please choose a file')
    }
    const contentType = file.type;
    const bucket = new S3({
      accessKeyId: 'AKIAYKBPCJIJPR26HL7T',
      secretAccessKey: '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',
      region: 'ap-south-1',
    });
    const params = {
      Bucket: 'botb',
      // Key: this.FOLDER + file.name,
      Key: file.name,
      Body: file,
      ACL: 'public-read',
      ContentType: contentType,
    };

    
    let self = this;
    bucket.upload(params, function (err, data) {
      if (err) {
        console.log('There was an error uploading your file: ', err);
        return false;
      }
     
        console.log('Successfully uploaded file.', data);
        self.judgeModel.profile_pic = data.Location;
        // self.productModel.image = data.Location;
        // self.onImageUpload.emit(data);
        self.fileNames=[];
        self.files = [];

        // self.ngOnInit();
      
      return data;
    });
  }

  addJudge(){
    console.log(this.judgeModel);
    this.judgeService.addJudge(this.judgeModel).subscribe((data)=>{
      console.log(data);
      this.toastr.success(data.message);
      this.dialogRef.close();
    },(error)=>{
      console.log(error);
      
    })
    
  }
}
